

import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Group from 'vux/src/components/group/index.vue'

import { mapState, mapGetters, mapMutations, mapActions } from "vuex";
import mobilePopup from "@/components/v-bind-mobile-popup.vue";
import MescrollVue from "mescroll.js/mescroll.vue";
export default {
components: {
Group,
XButton,
XHeader,
XInput,
Badge,
Checker,
CheckerItem,
mobilePopup,
MescrollVue
},
mounted() {

window.getAppInfo = (res) =>{
this.getAppInfo(res)
};
this.$nextTick(function(){
this.token = localStorage.getItem('token');
})
},
data() {
return {
token: '',
popupTitle: '钱包信息填写完成后不可修改',
popupInfo: '请确保填写的正确性',
openid: 'oOw4_6nymoZUJIFdyJZ6V2ORPk-E',
realName: '',
isShowPopup: false,
authorName: '',
authorImg: '',
mescrollDown: {
use: false
},
mescrollUp: {
use: false,
isBounce: false
}
};
},
computed:{
},
methods: {
...mapMutations(["switch_mobile_dialog"]),
...mapActions(["LoginInfo"]),
authorClick(){
this.$bridge.callhandler('appLogin', {}, (data) => {

console.log(data)
})
},
getAppInfo(data=''){

const resData = JSON.parse(data);
this.token = resData.token;
this.openid = resData.openid;
let params = {
token:  this.token,
api_name: 'wxLoginCallback',
gzh_openid: this.openid,
nickname: resData.screen_name,
headimgurl: resData.profile_image_url
}

this.$ajax({
method: "post",
url: "/api/hcashout/api",
data: this.$qs.stringify(params)
})
.then(res => {
console.log(res)
let data = res.data;
if(data.code == 1){
this.authorImg = data.data.headimgurl;
this.authorName = data.data.nickname;
}else{
this.$vux.toast.text(data.msg);
}
})
.catch(error => {
this.$vux.toast.text("服务器请求失败，请稍后重试");
});
},
hidePopup(){
this.isShowPopup = false;
},
comfirmSave(){
if (this.realName == '') {
this.$vux.toast.text("请填写真实姓名");
return;
}
if(this.authorName == '' || this.authorImg == ''){
this.$vux.toast.text("请授权");
return;
}
this.isShowPopup = true;
},
saveData(){

let params = {
token:  this.token,
api_name: 'perfect_wallet',
openid: this.openid,
user_real_name: this.realName,
package_name: localStorage.getItem('package_name')
}

this.$ajax({
method: "post",
url: "/api/hcashout/api",
data: this.$qs.stringify(params)
})
.then(res => {
console.log(res)
let data = res.data;
if(data.code == 1){
this.$vux.toast.text('保存成功');
setTimeout(() => {
this.$router.push('/');
}, 500);
}else{
this.$vux.toast.text(data.msg);
}
})
.catch(error => {
this.$vux.toast.text("服务器请求失败，请稍后重试");
});
}
}
};



